@import "./variables";

.footainer {
  color: white;
  display: flex;
  align-content: center;
  align-items: center;
  justify-content: center;
  height: 30px;
  max-width: $max-width;
  margin: 0 auto;
  text-align: center;
  background: linear-gradient(#D3AC8C, #8E6039);
  border-radius: 0 0 10px 10px;
  a {
    color: #F9DCC5;
    &:hover {
      color: #86435D;
    }
  }
  @media ($tablet) {
    margin: 0 10%;
  }
}